<template>
     <v-main>
          <v-breadcrumbs :items="breadCrumbsItems" divider="/"></v-breadcrumbs>
          <v-container>
               <v-card>
                    <v-card-title>
                              <v-row dense>
                                   <v-col cols="12" md="4">
                                        <v-select
                                             v-model="companies"
                                             :items="companyList"
                                             placeholder="Company"
                                             clearable
                                             outlined
                                             dense
                                        ></v-select>
                                   </v-col>
                              <v-spacer></v-spacer>
                              <v-btn @click="dialog = !dialog" color="primary"><v-icon left>mdi-plus</v-icon>New</v-btn>
                              </v-row>
                    </v-card-title>
                    <v-data-table 
                         :headers="headers" 
                         :items="filterData"
                         :loading="loading"
                         :search="searchData"
                         :page.sync="page"
                         loading-text="Loading Data. . .Please Wait"
                         @page-count="pageCount = $event"
                         hide-default-footer
                    >
                         <template v-slot:item.Status="{ item }">
                              <v-chip :color="item.Status == 1 ? 'success' : 'error'">
                                   {{item.Status == 1 ? 'Active' : 'Inactive'}}
                              </v-chip>
                         </template>
                         <template v-slot:item.actions="{ item }">
                              <v-btn @click="editRecord(item)" icon>
                                   <v-icon>mdi-pencil</v-icon>
                              </v-btn>
                              <v-btn @click="deleteRecord(item)" icon>
                                   <v-icon>mdi-delete</v-icon>
                              </v-btn>
                         </template>
                    </v-data-table>
                    <v-pagination
                         v-model="page"
                         :length="pageCount"
                         :total-visible="10"
                    ></v-pagination>
               </v-card>
          </v-container>
          <v-dialog v-model="dialog" width="500" persistent>
               <v-card>
                    <v-card-title>{{editMode == 0 ? 'New' : 'Edit'}} Account</v-card-title>
                    <v-divider></v-divider>
                    <v-container>
                         <v-form v-model="valid" ref="form" lazy-validation>
                              <v-card-text>
                                   <v-row align="center" justify="center" dense>
                                        <v-col cols="12" md="3">
                                             <v-text-field
                                                  v-model="editedAccount.EmployeeCode"
                                                  label="Code"
                                                  :readonly="editMode == 1"
                                                  :rules="[v => !!v || 'Code is required']"
                                                  outlined
                                                  dense
                                                  @blur="getEmployeeFullname()"
                                             ></v-text-field>
                                        </v-col>
                                        <v-col cols="12" md="9">
                                             <v-text-field
                                                  v-model="editedAccount.Fullname"
                                                  label="Name"
                                                  readonly
                                                  outlined
                                                  dense
                                             >
                                                  <template v-slot:append>
                                                       <v-fade-transition leave-absolute>
                                                            <v-progress-circular
                                                            v-if="loadName"
                                                            size="24"
                                                            color="primary"
                                                            indeterminate
                                                            ></v-progress-circular>
                                                       </v-fade-transition>
                                                  </template>
                                             </v-text-field>
                                        </v-col>
                                        <v-col cols="12" md="12">
                                             <v-text-field
                                                  v-model="editedAccount.Username"
                                                  append-icon="mdi-account"
                                                  label="UserName"
                                                  :rules="[v => !!v || 'Username is required']"
                                                  outlined
                                                  dense
                                             ></v-text-field>
                                        </v-col>
                                        <v-col cols="12" md="6">
                                             <v-text-field
                                                  v-model="newPassword"
                                                  append-icon="mdi-lock"
                                                  label="Password"
                                                  type="password"
                                                  :rules="[v => !!v || 'Password is required']"
                                                  outlined
                                                  dense
                                             ></v-text-field>
                                        </v-col>
                                        <v-col cols="12" md="6">
                                             <v-text-field
                                                  v-model="confirmedPassword"
                                                  append-icon="mdi-lock-check"
                                                  label="Confirm Password"
                                                  type="password"
                                                  :rules="passwordRules"
                                                  outlined
                                                  dense
                                             ></v-text-field>
                                        </v-col>
                                        <v-col cols="12" md="6">
                                             <v-text-field
                                                  v-model="editedAccount.IPAddr"
                                                  append-icon="mdi-ip"
                                                  label="IP Address"
                                                  :rules="[v => !!v || 'IP Address is required']"
                                                  outlined
                                                  dense
                                             ></v-text-field>
                                        </v-col>
                                        <v-col cols="12" md="6">
                                             <v-select
                                                  v-model="editedAccount.UserLevel"
                                                  :items="userLevel"
                                                  label="Level"
                                                  outlined
                                                  dense
                                             ></v-select>
                                        </v-col>
                                   </v-row>
                              </v-card-text>
                         </v-form>
                    </v-container>
                    <v-card-actions>
                         <v-spacer></v-spacer>
                         <v-btn @click="saveRecord()" color="primary" :disabled="disabled">
                              <v-icon left>mdi-content-save</v-icon>Save
                         </v-btn>
                         <v-btn @click="discardRecord()" text>
                              <v-icon left>mdi-delete</v-icon>Discard
                         </v-btn>
                    </v-card-actions>
               </v-card>
          </v-dialog>
     </v-main>
</template>

<script>
export default {
     data() {
          return {
               valid: true,
               loading: true,
               loadName: false,
               dialog: false,
               disabled: false,
               pageCount: 0,
               page: 1,
               editMode: 0,
               newPassword: '',
               confirmedPassword: '',
               accounts: [],
               notifications: [],
               passwordRules: [
                    v => !!v || 'Password is required',
                    v => (v == this.newPassword) || 'Password do not match'
               ],
               editedAccount: {
                    EmployeeCode: '',
                    Username: '',
                    Fullname: '',
                    IPAddr: '',
                    UserLevel: 0
               },
               companies: [],
               userLevel: [0, 1, 2, 9],
               saveOptions: {
                    title: 'Are you sure?',
                    text: "You won't be able to revert this!",
                    icon: 'warning',
                    showDenyButton: true,
                    showCancelButton: true,
                    confirmButtonText: 'Save',
                    denyButtonText: `Don't Save`
               },
               headers: [
                    {text: 'Code', value: 'EmployeeCode'},
                    {text: 'Name', value: 'Fullname'},
                    {text: 'Username', value: 'Username'},  
                    {text: 'IP Address', value: 'IPAddr'},
                    {text: 'Level', value: 'UserLevel'},
                    {text: 'Status', value: 'Status'},
                    {text: 'Actions', value: 'actions'}
               ],
               breadCrumbsItems: [
                    {text: 'Maintenance', disabled: false, href: '/accounts'},
                    {text: 'User Accounts', disabled: true, href: '/accounts'}
               ]
          }
     },
     created() {
          this.loadAccounts()
     },
     sockets: {
          notifications(data) {
               console.log(data)
               this.loadAccounts()
          }
     },
     computed: {
          filterData() {
               return this.accounts.filter(rec => {
                    return rec.ShortName.includes(this.companies || '')
               })
          },
          companyList() {
               return this.accounts.map(rec => {
                    return rec.ShortName
               })
          }
     },
     methods: {
          loadAccounts() {
               this.loading = true
               this.axios.get(`${this.api}/usercontrol`).then(res => {
                    this.accounts = res.data
                    this.loading = false
               })
          },
          getEmployeeFullname() {
               this.loadName = true
               this.disabled = false
               let data = this.accounts.filter(rec => (rec.EmployeeCode == this.editedAccount.EmployeeCode))
               if(data.length == 0 && this.editedAccount.EmployeeCode != '') {
                    this.axios.get(`${this.api}/employeeinfo/${this.editedAccount.EmployeeCode}`).then(res => {
                         if(res.data.length != 0) {
                              this.$refs.form.resetValidation()
                              this.editedAccount.Fullname = res.data[0].EmployeeName
                         } else {
                              this.editedAccount.Fullname = ''
                              this.disabled = true
                         }
                         this.loadName = false
                    })
               } else {
                    this.editedAccount = Object.assign({}, data[0])
                    this.loadName = false
               }
               this.disabled = false
          },
          saveRecord() {
               if(this.$refs.form.validate()) {
                    this.swal.fire(this.saveOptions).then(result => {
                         if(result.isConfirmed) {
                              let body = {
                                   procedureName: 'ProcUserControl',
                                   values: [
                                        this.editedAccount.EmployeeCode,
                                        this.editedAccount.Username,
                                        this.editedAccount.Fullname,
                                        this.md5(this.newPassword),
                                        this.editedAccount.IPAddr,
                                        this.editedAccount.UserLevel,
                                        this.moment().format('YYYY-MM-DD'),
                                        this.moment().format('YYYY-MM-DD'),
                                        this.userInfo.EmployeeCode,
                                        1
                                   ]
                              }
                              this.axios.post(`${this.api}/execute`, {data: JSON.stringify(body)})
                              this.swal.fire('Hooray!','Changes has been saved', 'success')
                              this.discardRecord()
                         } else if(result.isDenied) {
                              this.discardRecord()
                              this.swal.fire('Oh no!', 'Changes are not saved', 'info')
                         }
                    })
               }
          },
          editRecord(val) {
               this.editedAccount = Object.assign({}, val)
               this.dialog = true
               this.disabled = false
               this.editMode = 1
          },
          deleteRecord(val) {
               this.swal.fire({
                    title: 'Are you sure?',
                    text: val.Status == 1 ? "This account wil set to Inactive." : "This wil restore the account.",
                    icon: 'warning',
                    showCancelButton: true,
                    confirmButtonText: val.Status == 1 ? 'Yes, delete it!' : 'Yes, restore it!',
                    denyButtonText: 'Cancel'
               }).then(result => {
                    console.log(result)
                    if(result.isConfirmed) {
                         let body = {
                              procedureName: 'ProcUserControl',
                              values: [
                                   val.EmployeeCode,
                                   val.Username,
                                   val.Fullname,
                                   this.md5(this.newPassword),
                                   val.IPAddr,
                                   val.UserLevel,
                                   this.moment().format('YYYY-MM-DD'),
                                   this.moment().format('YYYY-MM-DD'),
                                   this.userInfo.EmployeeCode,
                                   0
                              ]
                         }
                         this.axios.post(`${this.api}/execute`, {data: JSON.stringify(body)})
                         this.swal.fire('Confirmed!','Changes has been saved', 'success')
                         this.recordLogging('Deleted!')
                         this.discardRecord()
                    }
               })
          },
          discardRecord() {
               this.editedAccount = {
                    EmployeeCode: '',
                    Username: '',
                    Fullname: '',
                    IPAddr: '',
                    UserLevel: 0,
               }
               this.loadAccounts()
               this.newPassword = ''
               this.confirmedPassword = ''
               this.dialog = false
               this.disabled = false
               this.editMode = 0
               this.$refs.form.resetValidation()
          },
          recordLogging(details) {
               let body = {
                    procedureName: 'ProcUserLogging',
                    values: [
                              this.$socket.id, 
                              this.userInfo.EmployeeCode,
                              `User: ${this.userInfo.EmployeeCode} ${details}`,
                              this.moment().format('YYYY-MM-DD hh:mm:ss'),
                              this.moment().format('YYYY-MM-DD hh:mm:ss'),
                              this.userInfo.EmployeeCode,
                         ]
               }
               this.notifications.push({
                    id: this.$socket.id,
                    user: this.userInfo.EmployeeCode
               })
               this.$socket.emit('notifications', this.notifications)
               this.axios.post(`${this.api}/execute`, {data: JSON.stringify(body)})
          }  
     }
}
</script>